import supabase from '../config/supabaseClient'
import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'

const Home = () => {
  // supabase
  const [fetchError, setFetchError] = useState(null)
  const [questions, setQuestions] = useState([])

  // UI
  const [subject, setSubject] = useState('')
  const [type, setType] = useState('')
  const [keyword, setKeyword] = useState('')
  const [filteredQuestions, setFilteredQuestions] = useState([])


  useEffect(() => {
    // supabase
    const fetchQuestions = async () => {
      const { data, error } = await supabase
        .from('question') // 
        .select()
      if (error) {
        setFetchError('Could not fetch the questions')
        setQuestions([])
      }
      if (data) {
        setQuestions(data)
        setFetchError(null)
      }
    }

    fetchQuestions()
  }, [])

  // 
  const handleSearch = () => {
    // 
    console.log(`${subject || ''}${type || ''}${keyword}`)
    let filtered = questions;

    if (subject || type) {
      filtered = questions.filter(q =>
        (subject ? q.subject === subject : true) &&
        (type ? q.type === type : true)
      )
    }
    if (keyword) {
      let score = 0;
      let ranking = [];
      let tkeyword = keyword.split('');
      filtered.forEach(q => {
        if(q.content.includes(keyword)) {
          score += 114514;
          return;
        }
        let tcontent = q.content.split('');
        for (var i = 0; i < tcontent.length; i++) {
          for (var j = 0; j < tkeyword.length; j++) {
            if (tcontent[i] === tkeyword[j]) {
              score++;
            }
          }
        }
        ranking.push({ id: q.id, score: score });
        score = 0;
      })
      ranking.sort((a, b) => b.score - a.score);
      ranking = ranking.filter(r => r.score > 0);
      filtered = ranking.map(rank => {
        return questions.find(q => q.id === rank.id);
      }).filter(Boolean);
    } 

    setFilteredQuestions(filtered)
  }

  return (
    <div className="container" style={{ maxWidth: 1200, margin: '0 auto', padding: 20 }}>
      {/*  */}
      <div className="nav" style={{ marginBottom: 20 }}>
        <Link to='/' className="nav-link" style={{ color: '#3498db', textDecoration: 'none', fontSize: 16 }}> </Link>
      </div>

      {/*  */}
      <h1 className="title" style={{ fontSize: 26, textAlign: 'center', marginBottom: 30, color: '#2c3e50' }}></h1>

      {/*  */}
      <div className="card" style={{ background: 'white', borderRadius: 8, boxShadow: '0 2px 6px rgba(0,0,0,0.06)', padding: 20, marginBottom: 25 }}>
        <h2 className="card-title" style={{ fontSize: 18, marginBottom: 15, color: '#2c3e50' }}></h2>
        <div className="filter" style={{ display: 'flex', flexWrap: 'wrap', gap: 15, alignItems: 'center' }}>
          <div className="filter-item" style={{ flex: 1, minWidth: 180 }}>
            <label style={{ display: 'block', marginBottom: 6, fontSize: 15 }}></label>
            <select className="input" value={subject} onChange={e => setSubject(e.target.value)} style={{ width: '100%', padding: 10, border: '1px solid #ddd', borderRadius: 6, fontSize: 16 }}>
              <option value=""></option>
              <option value="chinese"></option>
              <option value="math"></option>
              <option value="english"></option>
              <option value="physics"></option>
              <option value="chemistry"></option>
              <option value="politics"></option>
              <option value="history"></option>
              <option value="biology"></option>
              <option value="geography"></option>
            </select>
          </div>
          <div className="filter-item" style={{ flex: 1, minWidth: 180 }}>
            <label style={{ display: 'block', marginBottom: 6, fontSize: 15 }}></label>
            <select className="input" value={type} onChange={e => setType(e.target.value)} style={{ width: '100%', padding: 10, border: '1px solid #ddd', borderRadius: 6, fontSize: 16 }}>
              <option value=""></option>
              <option value="single"></option>
              <option value="multiple"></option>
              <option value="fill"></option>
              <option value="essay"></option>
            </select>
          </div>
          <div className="search-item" style={{ flex: 2, minWidth: 250 }}>
            <label style={{ display: 'block', marginBottom: 6, fontSize: 15 }}></label>
            <div style={{ display: 'flex', gap: 8 }}>
              <input type="text" className="input" value={keyword} onChange={e => setKeyword(e.target.value)} placeholder=""
                style={{ width: '100%', padding: 10, border: '1px solid #ddd', borderRadius: 6, fontSize: 16 }} />
              <button className="search-btn" onClick={handleSearch}
                style={{ padding: 10, background: '#3498db', color: 'white', border: 'none', borderRadius: 6, fontSize: 16, cursor: 'pointer' }} >
                
              </button>
            </div>
          </div>
        </div>
      </div>

      {/*  */}
      <div className="card" style={{ background: 'white', borderRadius: 8, boxShadow: '0 2px 6px rgba(0,0,0,0.06)', padding: 20 }}>
        <h2 className="card-title" style={{ fontSize: 18, marginBottom: 15, color: '#2c3e50' }}></h2>
        {fetchError && <p>{fetchError}</p>}
        <div className="question-list" style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(300px, 1fr))', gap: 20 }}>
          {filteredQuestions && filteredQuestions.length > 0 ? (
            filteredQuestions.map((q, idx) => (
              <div className="question-card" key={q.id || idx} style={{ border: '1px solid #eee', borderRadius: 6, padding: 15, background: 'white' }}>
                <span className="subject-tag" style={{ display: 'inline-block', padding: '3px 10px', background: '#e3f2fd', color: '#3498db', borderRadius: 20, fontSize: 14, marginBottom: 10 }}>
                  {{
                    chinese: '',
                    math: '',
                    english: '',
                    physics: '',
                    chemistry: '',
                    politics: '',
                    history: '',
                    biology: '',
                    geography: ''
                  }[q.subject] || q.subject || ''}
                </span>
                <span className="type-tag" style={{
                  display: 'inline-block',
                  padding: '4px 14px',
                  background: '#ffe0b2',
                  color: '#ef6c00',
                  borderRadius: 20,
                  fontSize: 14,
                  fontWeight: 600
                }}>
                  {q.type === 'single' && ''}
                  {q.type === 'multiple' && ''}
                  {q.type === 'fill' && ''}
                  {q.type === 'essay' && ''}
                  {!['single', 'multiple', 'fill', 'essay'].includes(q.type) && (q.type || '')}
                </span>
                <div className="question" style={{ fontSize: 16, marginBottom: 10, lineHeight: 1.6 }}>
                  {q.content || ''}
                </div>
                {q.imageurl && (
                  <div className="question-image" style={{ marginBottom: 10 }}>
                    <img
                      src={q.imageurl}
                      alt=""
                      style={{ maxWidth: '100%', maxHeight: 220, borderRadius: 6, border: '1px solid #eee', display: 'block', marginLeft: 0 }}
                    />
                  </div>

                )}
                <div className="answer" style={{ fontSize: 15, marginBottom: 8, color: '#666' }}>
                  <span style={{ fontWeight: 'bold', color: '#27ae60' }}></span>
                  {q.canswer || ''}
                </div>
                <div className="answer" style={{ fontSize: 15, marginBottom: 8, color: '#666' }}>
                  <span style={{ fontWeight: 'bold', color: '#e67e22' }}></span>
                  {q.eanswer || ''}
                </div>
                <div className="answer" style={{ fontSize: 15, marginBottom: 8, color: '#666' }}>
                  <span style={{ fontWeight: 'bold', color: '#2dbbceff' }}></span>
                  {q.analysis || ''}
                </div>
                <div className="meta" style={{ fontSize: 14, color: '#999', marginTop: 10 }}>
                  {q.created_at.slice(0, 19) || ''} | {q.author || ''}
                </div>
              </div>
            ))
          ) : (
            <p></p>
          )}
        </div>
      </div>
    </div>
  )
}

export default Home