import { useState } from "react"
import { useNavigate } from "react-router-dom"
import supabase from "../config/supabaseClient"

const Upload = () => {
  const navigate = useNavigate()

  //  state
  const [subject, setSubject] = useState('')
  const [questionType, setQuestionType] = useState('')
  const [questionContent, setQuestionContent] = useState('')
  const [wrongAnswer, setWrongAnswer] = useState('')
  const [correctAnswer, setCorrectAnswer] = useState('')
  const [analysis, setAnalysis] = useState('')
  const [questionImg, setQuestionImg] = useState(null)
  const [formError, setFormError] = useState(null)

  const [userEmail, setUserEmail] = useState('')
  useState(() => {
    supabase.auth.getUser().then(({ data: { user } }) => {
      setUserEmail(user?.email || '')
    })
  }, [])

  // 
  const handleFileChange = (e) => {
    setQuestionImg(e.target.files[0])
  }

  // 
  const handleDrop = (e) => {
    e.preventDefault()
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      setQuestionImg(e.dataTransfer.files[0])
    }
  }

  // 
  const handleDragOver = (e) => {
    e.preventDefault()
  }

  // 
  const handleSubmit = async (e) => {
    e.preventDefault()
    if (!subject || !questionType || !questionContent || !wrongAnswer || !correctAnswer) {
      setFormError(' * ')
      return
    }

    //  Supabase Storage
    let imgUrl = null
    if (questionImg) {
      const fileExt = questionImg.name.split('.').pop()
      const fileName = `${Date.now()}.${fileExt}`
      const { data: uploadData, error: uploadError } = await supabase
        .storage
        .from('questionimg')
        .upload(fileName, questionImg)
      if (uploadError) {
        setFormError('')
        return
      }
      imgUrl = "https://hqzemultusietooosnxt.supabase.co/storage/v1/object/public/questionimg/"+uploadData?.path
    }

    // 
    const { data, error } = await supabase
      .from('question')
      .insert([{
        subject,
        type: questionType,
        content: questionContent,
        eanswer: wrongAnswer,
        canswer: correctAnswer,
        analysis,
        imageurl: imgUrl,
        author: userEmail || ''
      }])

    if (error) {
      setFormError('')
      return
    }
    setFormError(null)
    navigate('/')
  }
  return (
    <div
      className="container"
      style={{
        maxWidth: 900,
        margin: '0 auto',
        padding: '32px 12px 60px 12px',
        background: '#f7f9fb',
        minHeight: '100vh'
      }}
    >
      <div className="nav-bar" style={{ marginBottom: 18 }}>
        <a href="search.html" className="nav-link" style={{
          color: '#1976d2',
          fontWeight: 500,
          textDecoration: 'none',
          fontSize: 16
        }}> </a>
      </div>
      <h1 className="page-title" style={{
        fontSize: 32,
        fontWeight: 700,
        color: '#1976d2',
        marginBottom: 28,
        textAlign: 'center',
        letterSpacing: 1
      }}></h1>
      <div className="card" style={{
        background: '#fff',
        borderRadius: 16,
        boxShadow: '0 4px 24px rgba(25, 118, 210, 0.07)',
        padding: '32px 24px',
        marginBottom: 32
      }}>
        <h2 className="card-title" style={{
          fontSize: 20,
          fontWeight: 600,
          color: '#1976d2',
          marginBottom: 18
        }}></h2>
        <form id="questionForm" onSubmit={handleSubmit}>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label htmlFor="subject" style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}> <span style={{ color: "#e74c3c" }}>*</span></label>
            <select id="subject" className="form-control" required value={subject} onChange={e => setSubject(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 14px',
                border: '1.5px solid #e3eaf2',
                borderRadius: 8,
                fontSize: 16,
                background: '#fafdff',
                marginTop: 4
              }}>
              <option value="">--  --</option>
              <option value="chinese"></option>
              <option value="math"></option>
              <option value="english"></option>
              <option value="physics"></option>
              <option value="chemistry"></option>
              <option value="politics"></option>
              <option value="history"></option>
              <option value="biology"></option>
              <option value="geography"></option>
            </select>
          </div>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label htmlFor="questionType" style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}> <span style={{ color: "#e74c3c" }}>*</span></label>
            <select id="questionType" className="form-control" required value={questionType} onChange={e => setQuestionType(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 14px',
                border: '1.5px solid #e3eaf2',
                borderRadius: 8,
                fontSize: 16,
                background: '#fafdff',
                marginTop: 4
              }}>
              <option value="">--  --</option>
              <option value="single"></option>
              <option value="multiple"></option>
              <option value="fill"></option>
              <option value="essay"></option>
            </select>
          </div>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label htmlFor="questionContent" style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}> <span style={{ color: "#e74c3c" }}>*</span></label>
            <input id="questionContent" className="form-control" placeholder="" required value={questionContent} onChange={e => setQuestionContent(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 14px',
                border: '1.5px solid #e3eaf2',
                borderRadius: 8,
                fontSize: 16,
                background: '#fafdff',
                marginTop: 4,
                minHeight: 70,
                resize: 'vertical'
              }} />
          </div>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label htmlFor="wrongAnswer" style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}> <span style={{ color: "#e74c3c" }}>*</span></label>
            <input id="wrongAnswer" className="form-control" placeholder="" required value={wrongAnswer} onChange={e => setWrongAnswer(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 14px',
                border: '1.5px solid #e3eaf2',
                borderRadius: 8,
                fontSize: 16,
                background: '#fafdff',
                marginTop: 4
              }} />
          </div>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label htmlFor="correctAnswer" style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}> <span style={{ color: "#e74c3c" }}>*</span></label>
            <input id="correctAnswer" className="form-control" placeholder="" required value={correctAnswer} onChange={e => setCorrectAnswer(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 14px',
                border: '1.5px solid #e3eaf2',
                borderRadius: 8,
                fontSize: 16,
                background: '#fafdff',
                marginTop: 4,
                minHeight: 50,
                resize: 'vertical'
              }} />
          </div>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label htmlFor="analysis" style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}></label>
            <input id="analysis" className="form-control" placeholder="" value={analysis} onChange={e => setAnalysis(e.target.value)}
              style={{
                width: '100%',
                padding: '12px 14px',
                border: '1.5px solid #e3eaf2',
                borderRadius: 8,
                fontSize: 16,
                background: '#fafdff',
                marginTop: 4,
                minHeight: 50,
                resize: 'vertical'
              }} />
          </div>
          <div className="form-group" style={{ marginBottom: 22 }}>
            <label style={{
              fontWeight: 500,
              marginBottom: 7,
              display: 'block',
              color: '#34495e'
            }}></label>
            <div
              className="upload-area"
              id="uploadArea"
              onClick={() => document.getElementById('questionImg').click()}
              onDrop={handleDrop}
              onDragOver={handleDragOver}
              style={{
                border: '2px dashed #90caf9',
                borderRadius: 10,
                background: '#f1f8fe',
                padding: '32px 0',
                textAlign: 'center',
                cursor: 'pointer',
                transition: 'border 0.2s, background 0.2s',
                marginTop: 6,
                color: '#1976d2',
                fontSize: 17,
                fontWeight: 500
              }}
            >
              <div className="upload-text">
                {questionImg ? questionImg.name : "JPG/PNG"}
              </div>
              <input
                type="file"
                id="questionImg"
                accept="image/jpg,image/png"
                style={{ display: "none" }}
                onChange={handleFileChange}
              />
            </div>
          </div>
          <button type="submit" className="btn submit-btn" id="submitBtn"
            style={{
              background: 'linear-gradient(90deg, #1976d2 60%, #42a5f5 100%)',
              color: '#fff',
              border: 'none',
              borderRadius: 8,
              padding: '12px 28px',
              fontSize: 18,
              fontWeight: 600,
              cursor: 'pointer',
              transition: 'background 0.2s, box-shadow 0.2s',
              boxShadow: '0 2px 8px rgba(25, 118, 210, 0.08)',
              marginTop: 8
            }}
          ></button>
          {formError && <p className="error" style={{ color: "#e74c3c", marginTop: "10px", fontSize: 16 }}>{formError}</p>}
        </form>
      </div>
    </div>
  )
}

export default Upload